/**
 * the error block model.
 */
export class ErrorBlockModel {

    /**
     * shows the error message.
     */
    public showError: boolean;

    /**
     * the error message.
     */
    public errorMessage: string;

    /**
     * the error title.
     */
    public errorTitle: string;

}


